let HOST_API = 'http://localhost:3003';



async function api_login_Async(phone, pass) {
    try {
        const response = await axios.get(`${HOST_API}/login`, {
            params: { phone: phone, pass : pass } // Axios tự biến cái này thành ?phone=1
        });

        const res = response.data; 
        return res;
    } catch (error) {
        // Axios xử lý lỗi rất hay, bạn có thể kiểm tra error.response
        if (error.response) {
            console.error("Lỗi từ Server:", error.response.data.message);
        } else {
            console.error("Lỗi kết nối:", error.message);
        }
        return undefined;
    }
    return undefined;
}

